
.page-user-info .dialog-mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9;
}
.page-user-info .dialog-mask .info-dialog {
  position: absolute;
  width: 7.73333333rem;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.page-user-info .dialog-mask .info-dialog-con {
  width: 100%;
  background: #FFFFFF;
  border-radius: 0.16rem;
  overflow: hidden;
  margin-bottom: 0.64rem;
  padding-bottom: 0.66666667rem;
}
.page-user-info .dialog-mask .info-dialog-con .user-img {
  text-align: center;
  margin: 0.74666667rem 0 0.29333333rem 0;
}
.page-user-info .dialog-mask .info-dialog-con .user-img img {
  width: 1.17333333rem;
  height: 1.17333333rem;
  border-radius: 0.66666667rem;
}
.page-user-info .dialog-mask .info-dialog-con .tip {
  font-size: 0.45333333rem;
  color: #030303;
  text-align: center;
  font-weight: bold;
  margin-bottom: 0.61333333rem;
}
.page-user-info .dialog-mask .info-dialog-con .info-input {
  padding: 0 0.64rem;
}
.page-user-info .dialog-mask .info-dialog-con .info-input input {
  width: 6.45333333rem;
  height: 0.96rem;
  margin-top: 0.21333333rem;
  background: #F7F7F7;
  border-radius: 0.05333333rem;
  font-size: 0.37333333rem;
  padding: 0 0.21333333rem;
  color: #333;
}
.page-user-info .dialog-mask .info-dialog-con .info-input input::-webkit-input-placeholder {
  color: #CCCCCC;
  font-weight: 300;
}
.page-user-info .dialog-mask .info-dialog-con .info-input input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #CCCCCC;
  font-weight: 300;
}
.page-user-info .dialog-mask .info-dialog-con .info-input input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #CCCCCC;
  font-weight: 300;
}
.page-user-info .dialog-mask .info-dialog-con .info-input input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #CCCCCC;
  font-weight: 300;
}
.page-user-info .dialog-mask .info-dialog-con .info-input .prompt {
  font-size: 0.32rem;
  font-weight: 300;
  color: #999999;
}
.page-user-info .dialog-mask .info-dialog-con .info-input .select-address {
  position: relative;
  /*i {
              background: url(../../assets/img/more@3x.png) no-repeat;
              background-size: 100%;
              display: inline-block;
              width: 24px;
              height: 6px;
              position: absolute;
              right: 16px;
              top: 50px;
              color: #999;
            }*/
}
.page-user-info .dialog-mask .info-dialog-con .info-input .select-address input {
  padding-right: 1.01333333rem;
}
.page-user-info .dialog-mask .info-dialog-con .info-input .select-address .more {
  position: absolute;
  width: 1.33333333rem;
  right: 0.34666667rem;
  top: 0.29333333rem;
  color: #999;
  text-align: right;
}
.page-user-info .dialog-mask .info-dialog-con .info-input .select-address .more span {
  display: inline-block;
  font-size: 0.66666667rem;
  width: 0.4rem;
  margin-right: -0.24rem;
}
.page-user-info .dialog-mask .info-dialog-con .submitBtn {
  position: relative;
  text-align: center;
  margin-top: 0.64rem;
  z-index: 1;
}
.page-user-info .dialog-mask .info-dialog-con .submitBtn span {
  position: relative;
  display: inline-block;
  width: 4.66666667rem;
  padding: 0.24rem 0;
  background: #E9E9E9;
  border-radius: 1.33333333rem;
  font-size: 0.37333333rem;
  color: #CCCCCC;
}
.page-user-info .dialog-mask .info-dialog-con .submitBtn span:after {
  content: '';
  position: absolute;
  top: 40%;
  left: 5%;
  width: 90%;
  height: 50%;
  z-index: -1;
  -webkit-box-shadow: 0 0.13333333rem 0.4rem rgba(230, 230, 230, 0.8);
          box-shadow: 0 0.13333333rem 0.4rem rgba(230, 230, 230, 0.8);
}
.page-user-info .dialog-mask .info-dialog-con .submitBtn span.enableBtn {
  background: #FF3434;
  color: #FFFFFF;
}
.page-user-info .dialog-mask .info-dialog-con .submitBtn span.enableBtn:after {
  -webkit-box-shadow: 0 0.13333333rem 0.4rem rgba(255, 0, 0, 0.4);
          box-shadow: 0 0.13333333rem 0.4rem rgba(255, 0, 0, 0.4);
}
.page-user-info .dialog-mask .info-dialog-close {
  text-align: center;
}
.page-user-info .dialog-mask .info-dialog-close i {
  background: url(../../assets/img/close@3x.png) no-repeat;
  background-size: 100%;
  display: inline-block;
  width: 0.85333333rem;
  height: 0.85333333rem;
}
.page-user-info .info-mask-enter-active,
.page-user-info .info-mask-leave-active {
  -webkit-transition: all ease .3s;
  transition: all ease .3s;
}
.page-user-info .info-mask-enter,
.page-user-info .info-mask-leave-to {
  opacity: 0;
}


