
.page-history {
  background: #f7f7f7;
}
.page-history .part-list {
  padding: 0.21333333rem 0.32rem;
}
.page-history .part-list .goods-panel {
  -webkit-box-shadow: 0 0.16rem 0.4rem 0 #f1f1f1;
          box-shadow: 0 0.16rem 0.4rem 0 #f1f1f1;
}
.page-history .part-end {
  text-align: center;
  padding: 0.32rem 0 0.53333333rem;
  font-size: 0.32rem;
  color: #ccc;
}
.page-history .part-end span {
  position: relative;
}
.page-history .part-end span:before,
.page-history .part-end span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: -0.86666667rem;
  height: 1PX;
  width: 0.66666667rem;
  background: -webkit-gradient(linear, left top, right top, from(transparent), to(#ccc));
  background: linear-gradient(to right, transparent 0%, #ccc 100%);
}
.page-history .part-end span:after {
  left: auto;
  right: -0.86666667rem;
  background: -webkit-gradient(linear, right top, left top, from(transparent), to(#ccc));
  background: linear-gradient(to left, transparent 0%, #ccc 100%);
}
.page-history .part-none {
  color: #333;
  font-size: 0.32rem;
  text-align: center;
  padding: 0.26666667rem 0;
}


