
@charset "UTF-8";
.jd-popup {
  /*背景过渡动画*/
  /*容器过渡动画*/
}
.jd-popup-back {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
  background: rgba(0, 0, 0, 0.6);
}
.jd-popup-panel {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}
.jd-popup .tra-back-enter-active,
.jd-popup .tra-back-leave-active {
  -webkit-transition: all ease .3s;
  transition: all ease .3s;
}
.jd-popup .tra-back-enter,
.jd-popup .tra-back-leave-to {
  opacity: 0;
}
.jd-popup .tra-panel-enter-active,
.jd-popup .tra-panel-leave-active {
  -webkit-transition: all ease .3s;
  transition: all ease .3s;
}
.jd-popup .tra-panel-enter,
.jd-popup .tra-panel-leave-to {
  opacity: 0;
  -webkit-transform: translate(-50%, -50%) scale(0.5);
          transform: translate(-50%, -50%) scale(0.5);
}


