
@charset "UTF-8";

/*样式重置*/

/*样式重置*/
html,
body,
ol,
ul,
li,
h1,
p {
  margin: 0;
  padding: 0;
}
ol,
ul {
  list-style: none;
}
a,
img,
button,
input,
textarea,
i,
span,
div {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
input,
select,
textarea {
  -webkit-appearance: none;
  padding: 0;
  border-style: none;
  -webkit-box-shadow: none;
          box-shadow: none;
}
a {
  text-decoration: none;
  color: inherit;
}
html {
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}
body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
body *,
body *:before,
body *:after {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
.myToast {
  position: fixed;
  z-index: 999;
  max-width: 40%;
  padding: 0.16rem 0.53333333rem;
  border-radius: 0.13333333rem;
  background: rgba(0, 0, 0, 0.4);
  color: #fff;
  top: 40%;
  left: 50%;
  font-size: 0.32rem;
  text-align: justify;
  -webkit-transform: translate(-50%, 70%);
          transform: translate(-50%, 70%);
  opacity: 0;
  -webkit-transition: all ease .3s;
  transition: all ease .3s;
}
.myLoading {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}
.myLoading div {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 0.33333333rem;
  height: 0.33333333rem;
}
.pageLoading {
  text-align: center;
  padding: 0.26666667rem;
  font-size: 0.32rem;
  color: #ccc;
}
.pageLoading i {
  display: inline-block;
  vertical-align: -0.06666667rem;
  width: 0.33333333rem;
  height: 0.33333333rem;
  margin-right: 0.06666667rem;
}

/*京东正黑体*/
@font-face {
  font-family: "jdzh-r";
  src: url("assets/fonts/JDZhengHei-01-Regular.woff"), url("assets/fonts/JDZhengHei-01-Regular.ttf"), url("assets/fonts/JDZhengHei-01-Regular.eot");
}
body {
  max-width: 12rem;
  margin: 0 auto;
}


