
@charset "UTF-8";
.page-grid {
  width: 9.33333333rem;
  margin: 0.53333333rem auto 0.4rem;
  background: rgba(255, 194, 171, 0.2);
  border-radius: 0.10666667rem;
  padding: 0 0.26666667rem 0.4rem;
}
.page-grid.page-grid--roll {
  position: relative;
  height: 6.50666667rem;
}
.page-grid.page-grid--roll .grid-list {
  position: absolute;
  top: 1.93333333rem;
  left: 0.26666667rem;
  width: 8.8rem;
}
.page-grid .grid-title {
  width: 4.26666667rem;
  height: 0.85333333rem;
  line-height: 0.85333333rem;
  font-size: 0.42666667rem;
  color: #FFEBC9;
  margin: 0 auto;
  text-align: center;
  background: rgba(255, 194, 171, 0.2);
  border-radius: 0 0 0.21333333rem 0.21333333rem;
}
.page-grid .grid-head {
  font-size: 0;
}
.page-grid .grid-head .head-part {
  font-size: 0.37333333rem;
  position: relative;
  width: 50%;
  height: 1.17333333rem;
  line-height: 1.17333333rem;
  color: #FFEBC9;
  display: inline-block;
}
.page-grid .grid-head .head-part i {
  display: inline-block;
  width: 0.46666667rem;
  height: 0.29333333rem;
  background: url(../../assets/img/zan@3x.png) no-repeat center;
  background-size: auto 100%;
  vertical-align: -0.04rem;
  margin-right: 0.10666667rem;
}
.page-grid .grid-head .head-part:nth-child(1):after {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -0.10666667rem;
  height: 0.21333333rem;
  border-right: 0.01333333rem solid #FFEBC9;
}
.page-grid .grid-head .head-part:nth-child(1) i {
  height: 0.33333333rem;
  vertical-align: -0.05333333rem;
  background: url(../../assets/img/friends@3x.png) no-repeat center;
  background-size: auto 100%;
}
.page-grid .grid-list {
  padding: 0 0.4rem;
  overflow: hidden;
  /*动画*/
}
.page-grid .grid-list .list-empty {
  position: relative;
  width: 2.4rem;
  height: 2.13333333rem;
  background: url(../../assets/img/cry@3x.png) no-repeat center;
  background-size: auto 100%;
  margin: 0.86666667rem auto 1.06666667rem;
}
.page-grid .grid-list .list-empty span {
  position: absolute;
  top: 2.53333333rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  color: #fff;
  font-size: 0.37333333rem;
  font-weight: 300;
  white-space: nowrap;
}
.page-grid .grid-list .list-item {
  position: relative;
  font-size: 0.34666667rem;
  color: #fff;
  line-height: 1.06666667rem;
  text-align: left;
}
.page-grid .grid-list .list-item .item-img {
  display: inline-block;
  width: 0.66666667rem;
  height: 0.66666667rem;
  background: #fff;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 0.13333333rem;
  overflow: hidden;
  border: 0.02666667rem solid #fff;
}
.page-grid .grid-list .list-item .item-img img {
  width: 100%;
  height: 0.66666667rem;
  border-radius: 50%;
}
.page-grid .grid-list .list-item .item-name {
  display: inline-block;
  max-width: 2.66666667rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-right: 0.13333333rem;
  vertical-align: middle;
}
.page-grid .grid-list .list-item .item-info {
  vertical-align: middle;
}
.page-grid .grid-list .list-item .item-time {
  position: absolute;
  top: 0;
  right: 0;
}
.page-grid .grid-list .list-item {
  -webkit-transition: all 1s;
  transition: all 1s;
}
.page-grid .grid-list .list-enter {
  opacity: 0;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.page-grid .grid-list .list-leave-to {
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.page-grid .grid-list .list-leave-active {
  width: 8rem;
  position: absolute;
}


