@-webkit-keyframes btnShake {
0% {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.03, 1.03, 1);
            transform: scale3d(1.03, 1.03, 1);
}
100% {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
@keyframes btnShake {
0% {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.03, 1.03, 1);
            transform: scale3d(1.03, 1.03, 1);
}
100% {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
