
@charset "UTF-8";
.page-home {
  position: relative;
  overflow: hidden;
  min-height: 18rem;
  background: #ff5f7d;
  /*顶部半圆背景*/
  /*轮播模块*/
  /*顶部标题*/
  /*模块分隔标题*/
  /*商品列表模块*/
  /*查看往期精彩*/
  /*推荐列表模块*/
  /*底部信息*/
  /*我的*/
}
.page-home .top-bac {
  position: absolute;
  top: 0;
  left: -25%;
  width: 150%;
  height: 4.26666667rem;
  background: -webkit-gradient(linear, left top, left bottom, from(#FF3566), to(#DE123A));
  background: linear-gradient(#FF3566 0%, #DE123A 100%);
  border-radius: 0 0 40% 40%;
}
.page-home .top-bac--petal {
  position: absolute;
  top: 6rem;
  left: 2.13333333rem;
  opacity: .7;
  width: 9.33333333rem;
  height: 9.33333333rem;
  background: url(../../assets/img/petal1.svg) no-repeat center;
  background-size: cover;
}
.page-home .top-bac--petal3 {
  position: absolute;
  top: 12.66666667rem;
  left: -3.06666667rem;
  opacity: .3;
  width: 9.33333333rem;
  height: 9.33333333rem;
  background: url(../../assets/img/petal3.svg) no-repeat center;
  background-size: cover;
}
.page-home .part-poll {
  position: relative;
  width: 9.33333333rem;
  height: 4.73333333rem;
  margin: 0.32rem auto;
  background: linear-gradient(-135deg, #FFF3F5 0%, #FFEFF2 100%);
  -webkit-box-shadow: 0 0.26666667rem 0.4rem 0 rgba(255, 46, 85, 0.5);
          box-shadow: 0 0.26666667rem 0.4rem 0 rgba(255, 46, 85, 0.5);
  border-radius: 0.10666667rem;
  overflow: hidden;
}
.page-home .part-poll .swiper-container {
  border-radius: 0.10666667rem;
  overflow: hidden;
}
.page-home .part-poll .poll-content {
  height: 4.73333333rem;
  background: linear-gradient(-135deg, #FFF3F5 0%, #FFEFF2 100%);
}
.page-home .part-poll .poll-content a {
  display: block;
  width: 100%;
  height: 100%;
}
.page-home .part-poll .poll-content a img {
  width: 100%;
  height: 100%;
  border-radius: 0.10666667rem;
}
.page-home .part-poll .swiper-slide {
  border-radius: 0.10666667rem;
}
.page-home .part-poll .swiper-pagination {
  text-align: right;
  padding: 0 0.26666667rem 0.13333333rem 0;
}
.page-home .part-poll .swiper-pagination .swiper-pagination-bullet {
  background: #fff;
  opacity: .5;
  border-radius: 50%;
}
.page-home .part-poll .swiper-pagination .swiper-pagination-bullet-active {
  background: #fff;
  opacity: 1;
}
.page-home .part-banner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.29333333rem;
  color: #fff;
  margin-bottom: 0.13333333rem;
}
.page-home .part-banner li {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.page-home .part-banner li:nth-child(-n+2):after {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  width: 0.02666667rem;
  height: 0.16rem;
  margin-top: -0.08rem;
  background: #fff;
  opacity: .8;
}
.page-home .part-banner li:before {
  content: '';
  display: inline-block;
  width: 0.26666667rem;
  height: 0.26666667rem;
  background: url(../../assets/img/check@3x.png) no-repeat center;
  background-size: auto 100%;
  margin-right: 0.13333333rem;
  vertical-align: -0.02666667rem;
}
.page-home .part-title {
  position: relative;
  font-size: 0.42666667rem;
  color: #fff;
  text-align: center;
  padding: 0.4rem;
  font-weight: bolder;
}
.page-home .part-title .star-left,
.page-home .part-title .star-right {
  display: inline-block;
  width: 1rem;
  height: 0.37333333rem;
  background: url(../../assets/img/star@3x.png) no-repeat center;
  background-size: auto 100%;
  vertical-align: -0.04rem;
  margin: 0 0.13333333rem;
}
.page-home .part-title .star-left {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.page-home .part-list {
  position: relative;
  z-index: 1;
  width: 9.33333333rem;
  margin: 0 auto;
}
.page-home .part-list > p {
  color: #fff;
  font-size: 0.32rem;
  text-align: center;
  padding: 0.4rem 0;
}
.page-home .part-go {
  position: relative;
  z-index: 1;
  text-align: center;
  margin: 0.53333333rem 0 0.37333333rem 0;
}
.page-home .part-go span {
  font-size: 0.37333333rem;
  font-weight: 300;
  border: 1PX solid #FFFFFF;
  border-radius: 0.48rem;
  padding: 0.13333333rem 0.4rem;
  color: #fff;
}
.page-home .part-recommend {
  position: relative;
  z-index: 1;
  width: 100%;
}
.page-home .part-bottom {
  position: relative;
  font-size: 0.32rem;
  color: #9f0022;
  margin: 0.66666667rem 0;
  text-align: center;
  opacity: .5;
}
.page-home .part-bottom > span {
  position: relative;
}
.page-home .part-bottom > span:before,
.page-home .part-bottom > span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: -0.86666667rem;
  height: 1PX;
  width: 0.66666667rem;
  background: -webkit-gradient(linear, left top, right top, from(transparent), to(#9F0022));
  background: linear-gradient(to right, transparent 0%, #9F0022 100%);
}
.page-home .part-bottom > span:after {
  left: auto;
  right: -0.86666667rem;
  background: -webkit-gradient(linear, right top, left top, from(transparent), to(#9F0022));
  background: linear-gradient(to left, transparent 0%, #9F0022 100%);
}
.page-home .mine {
  -webkit-transition: all ease .5s;
  transition: all ease .5s;
  width: 1.33333333rem;
  height: 1.33333333rem;
  position: fixed;
  bottom: 0.42666667rem;
  right: 0.42666667rem;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 50%;
  z-index: 9;
  text-align: center;
  padding-top: 0.30666667rem;
  -webkit-box-shadow: 0 0.13333333rem 0.26666667rem rgba(0, 0, 0, 0.2);
          box-shadow: 0 0.13333333rem 0.26666667rem rgba(0, 0, 0, 0.2);
}
.page-home .mine:before {
  content: '';
  position: absolute;
  top: -20%;
  left: -20%;
  width: 140%;
  height: 140%;
}
.page-home .mine:after {
  content: '';
  display: inline-block;
  width: 0.73333333rem;
  height: 0.73333333rem;
  background: url(../../assets/img/mine1@3x.png) no-repeat center;
  background-size: auto 100%;
}

/*安卓下一些特殊处理*/
.underAndroid .page-home .part-go span {
  padding-top: 0.2rem;
}


