
.jd-address .address-mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 99;
}
.jd-address .address-mask .otherArea {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
}
.jd-address .address-mask .address-dialog {
  width: 100%;
  height: 13.06666667rem;
  overflow: hidden;
  position: absolute;
  left: 0;
  bottom: 0;
  background: #fff;
  animation: mymove 300ms 1;
  -webkit-animation: mymove 300ms 1;
  /* Safari and Chrome */
  z-index: 999;
}
.jd-address .address-mask .address-dialog-title {
  width: 100%;
  height: 1.49333333rem;
  line-height: 1.49333333rem;
  text-align: center;
}
.jd-address .address-mask .address-dialog-title span {
  font-size: 0.4rem;
  color: #333333;
}
.jd-address .address-mask .address-dialog-con .loading {
  position: absolute;
  top: 50%;
  left: 48%;
}
.jd-address .address-mask .address-dialog-con .loading i {
  display: inline-block;
  width: 0.66666667rem;
  height: 0.66666667rem;
  background: url(data:image/gif;base64,) no-repeat;
  background-size: cover;
}
.jd-address .address-mask .address-dialog-con ul.con-select {
  height: 1.17333333rem;
  border-bottom: 0.01333333rem solid #F1F1F1;
  padding-left: 0.42666667rem;
}
.jd-address .address-mask .address-dialog-con ul.con-select li {
  float: left;
  font-size: 0.37333333rem;
  color: #333333;
  margin-right: 0.8rem;
  border-bottom: 0.06666667rem solid #fff;
  height: 1.12rem;
  line-height: 1.12rem;
  max-width: 1.89333333rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.jd-address .address-mask .address-dialog-con ul.con-select li.active {
  color: #FF3434;
  border-bottom-color: #FF3434;
}
.jd-address .address-mask .address-dialog-con ul.con-select li:last-child {
  margin-right: 0;
}
.jd-address .address-mask .address-dialog-con ul.area {
  height: 10.4rem;
  overflow: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  /* ios5+ */
}
.jd-address .address-mask .address-dialog-con ul.area li {
  float: left;
  padding-left: 0.42666667rem;
  width: 100%;
  height: 1.17333333rem;
  line-height: 1.17333333rem;
  font-size: 0.37333333rem;
  color: #333333;
}
.jd-address .address-mask .address-dialog-con ul.area li i {
  background: url(../../../assets/img/select@3x.png) no-repeat;
  background-size: 100%;
  display: inline-block;
  width: 0.32rem;
  height: 0.22666667rem;
  margin-left: 0.21333333rem;
}
.jd-address .address-mask .address-dialog-con ul.area li.selected {
  font-weight: bold;
}
@keyframes mymove {
0% {
    bottom: -13.06666667rem;
}
100% {
    bottom: 0;
}
}
@-webkit-keyframes mymove {
0% {
    bottom: -13.06666667rem;
}
100% {
    bottom: 0;
}
}
.fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}

/*.fade-leave-active {
  transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}*/
.fade-enter {
  -webkit-transform: translateX(6rem);
          transform: translateX(6rem);
  opacity: 0;
}
.fade-leave-to {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 0;
}


