
@charset "UTF-8";
.page-arrow {
  /*分享过渡动画*/
}
.page-arrow .share-bac {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9;
  background: rgba(0, 0, 0, 0.6);
}
.page-arrow .share-bac .share-panel {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  height: 100%;
  max-width: 12rem;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.page-arrow .share-bac .share-arrow {
  position: absolute;
  top: 0.26666667rem;
  right: 1.33333333rem;
  width: 4rem;
  height: 3.33333333rem;
}
.page-arrow .share-bac .share-p1 {
  font-size: 0.48rem;
  color: #fff;
  text-align: center;
  margin-top: 3.06666667rem;
}
.page-arrow .share-bac .share-p1 .share-heart {
  position: relative;
  top: 0.26666667rem;
  display: inline-block;
  width: 0.8rem;
  height: 1.2rem;
  background-image: url(../../assets/img/shareHeart@3x.png);
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.page-arrow .share-bac .share-p1 .share-gift {
  position: relative;
  top: 0.4rem;
  left: 0.06666667rem;
  display: inline-block;
  width: 0.8rem;
  height: 1.2rem;
  background-image: url(../../assets/img/gift@3x.png);
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.page-arrow .share-bac .share-p2 {
  font-size: 0.64rem;
  font-weight: bolder;
  color: #fff;
  text-align: center;
  margin-top: 0.26666667rem;
}
.page-arrow .share-bac .share-p2 span {
  color: #ffd491;
}
.page-arrow .tra-share-leave-active {
  -webkit-transition: all ease .3s;
  transition: all ease .3s;
}
.page-arrow .tra-share-leave-to {
  opacity: 0;
}


