
.page-rule {
  background: #fff;
  padding: 0.42666667rem;
}
.page-rule .part {
  padding: 0.26666667rem 0;
}
.page-rule .part-title {
  position: relative;
  z-index: 1;
  font-size: 0.42666667rem;
  font-weight: bolder;
  color: #000;
  margin-bottom: 0.4rem;
  max-height: 13.32rem;
}
.page-rule .part-title span {
  position: relative;
}
.page-rule .part-title span:after {
  content: '';
  position: absolute;
  bottom: -0.01333333rem;
  left: 0;
  width: 100%;
  height: 0.66666667rem;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 194, 194, 0)), to(rgba(255, 194, 194, 0.8)));
  background: linear-gradient(to bottom, rgba(255, 194, 194, 0) 0%, rgba(255, 194, 194, 0.8) 100%);
  z-index: -1;
  -webkit-transform-origin: center bottom;
          transform-origin: center bottom;
  -webkit-transform: perspective(13.33333333rem) rotateX(75deg);
          transform: perspective(13.33333333rem) rotateX(75deg);
}
.page-rule .part-tip {
  font-size: 0.37333333rem;
  color: #333;
  max-height: 13.32rem;
  line-height: 1.5;
  text-align: justify;
  margin: 0.26666667rem 0;
}
.page-rule .part-info {
  font-size: 0.37333333rem;
  color: #666;
  max-height: 13.32rem;
  line-height: 1.5;
  text-align: justify;
}
.page-rule .part-info li {
  list-style: initial;
  margin-left: 0.36rem;
  padding: 0.06666667rem 0;
}


