
@charset "UTF-8";
.page-mine {
  background: #fff;
}
.page-mine .list ul li {
  width: 10rem;
  height: 2.74666667rem;
  padding: 0.30666667rem;
  border-bottom: 1PX solid #F1F1F1;
}
.page-mine .list ul li .img-wrap {
  float: left;
  margin-right: 0.32rem;
  background: #efefef;
  width: 2.88rem;
  height: 2.13333333rem;
}
.page-mine .list ul li .img-wrap img {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.page-mine .list ul li .info-wrap .p-name {
  font-size: 0.37333333rem;
  font-weight: 300;
  color: #333333;
  height: 1.12rem;
  line-height: 0.56rem;
  margin-bottom: 0.37333333rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.page-mine .list ul li .info-wrap .p-state {
  height: 0.64rem;
  line-height: 0.64rem;
}
.page-mine .list ul li .info-wrap .p-state .time {
  float: left;
  font-size: 0.32rem;
  font-weight: 300;
  color: #999999;
}
.page-mine .list ul li .info-wrap .p-state .state {
  float: right;
}
.page-mine .list ul li .info-wrap .p-state .state-prize {
  position: relative;
  top: -0.02666667rem;
  display: inline-block;
  font-size: 0.37333333rem;
  padding: 0.06666667rem 0.37333333rem;
  text-align: center;
  background: #FF3434;
  border-radius: 1.22666667rem;
  color: #FFFFFF;
  font-weight: 300;
}
.page-mine .list ul li .info-wrap .p-state .state-on {
  color: #FF3434;
}
.page-mine .list ul li .info-wrap .p-state .state-end,
.page-mine .list ul li .info-wrap .p-state .state-get,
.page-mine .list ul li .info-wrap .p-state .state-nostart {
  color: #CCCCCC;
}
.page-mine .list ul li .info-wrap .p-state .state-get {
  color: #FF3434;
}
.page-mine .part-end {
  text-align: center;
  padding: 0.32rem 0 0.53333333rem;
  font-size: 0.32rem;
  color: #ccc;
}
.page-mine .part-end span {
  position: relative;
}
.page-mine .part-end span:before,
.page-mine .part-end span:after {
  content: '';
  position: absolute;
  top: 50%;
  left: -0.86666667rem;
  height: 1PX;
  width: 0.66666667rem;
  background: -webkit-gradient(linear, left top, right top, from(transparent), to(#ccc));
  background: linear-gradient(to right, transparent 0%, #ccc 100%);
}
.page-mine .part-end span:after {
  left: auto;
  right: -0.86666667rem;
  background: -webkit-gradient(linear, right top, left top, from(transparent), to(#ccc));
  background: linear-gradient(to left, transparent 0%, #ccc 100%);
}
.page-mine .part-none {
  color: #333;
  font-size: 0.32rem;
  text-align: center;
  padding: 0.26666667rem 0;
}
.page-mine .part-loading {
  text-align: center;
  padding: 0.26666667rem;
  font-size: 0.32rem;
  color: #ccc;
}
.page-mine .part-loading i {
  display: inline-block;
  vertical-align: -0.06666667rem;
  background: url("../../assets/img/loading.gif") no-repeat center;
  background-size: cover;
  width: 0.33333333rem;
  height: 0.33333333rem;
  margin-right: 0.06666667rem;
}

/*安卓下一些特殊处理*/
.underAndroid .page-mine .state-prize {
  padding-top: 0.09333333rem !important;
}


