
.page-success .success-bac {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 90;
}
.page-success .success-body {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
}
.page-success .success-body.isHide {
  z-index: -1 !important;
}
.page-success .success-panel {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-transition: all ease .3s;
  transition: all ease .3s;
  -webkit-transform: perspective(20rem) translate3d(0, 0, 0);
          transform: perspective(20rem) translate3d(0, 0, 0);
  opacity: 1;
}
.page-success .success-panel.isHide {
  -webkit-transform: perspective(20rem) translate3d(0, 0, 2.66666667rem);
          transform: perspective(20rem) translate3d(0, 0, 2.66666667rem);
  opacity: 0;
}
.page-success .success-img {
  margin: 2.13333333rem auto 0;
  width: 7.93333333rem;
  height: 8.53333333rem;
}
.page-success .success-img img {
  width: 7.93333333rem;
  height: 8.53333333rem;
}
.page-success .success-close {
  width: 0.86666667rem;
  height: 0.86666667rem;
  background: url(../../assets/img/close@3x.png) no-repeat center;
  background-size: 100% auto;
  margin: 0.66666667rem auto 0;
}


