
.page-recommend {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}
.page-recommend ul {
  white-space: nowrap;
  font-size: 0;
}
.page-recommend .recommend-panel {
  display: inline-block;
  background: #fff;
  width: 4rem;
  height: 6.26666667rem;
  -webkit-box-shadow: 0 0.16rem 0.4rem 0 rgba(255, 52, 52, 0.05);
          box-shadow: 0 0.16rem 0.4rem 0 rgba(255, 52, 52, 0.05);
  border-radius: 0.05333333rem;
  overflow: hidden;
}
.page-recommend .recommend-panel a {
  display: inline-block;
}
.page-recommend .recommend-panel + .recommend-panel {
  margin-left: 0.16rem;
}
.page-recommend .recommend-panel:nth-child(1) {
  margin-left: 0.32rem;
}
.page-recommend .recommend-panel:nth-last-child(1) {
  margin-right: 0.32rem;
}
.page-recommend .recommend-img {
  height: 4rem;
  background: #efefef;
  overflow: hidden;
}
.page-recommend .recommend-img img {
  width: 100%;
  height: 100%;
}
.page-recommend .recommend-text {
  max-height: 1.17333333rem;
  padding: 0.21333333rem 0.21333333rem 0 0.21333333rem;
  white-space: pre-wrap;
  word-break: break-all;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  font-size: 0.37333333rem;
  font-weight: 300;
  line-height: 1.3;
  color: #333;
}
.page-recommend .recommend-price {
  font-family: 'jdzh-r';
  padding: 0.26666667rem 0 0 0.21333333rem;
  font-size: 0.32rem;
  color: #FF3434;
}
.page-recommend .recommend-price span {
  position: relative;
  font-size: 0.48rem;
  top: 0.01333333rem;
  left: 0.04rem;
  padding-top: 0.13333333rem;
}


