
@charset "UTF-8";
.page-goods-list .goods-panel {
  position: relative;
  padding: 0.42666667rem 0.32rem 0.32rem;
  background: #fff;
  -webkit-box-shadow: 0 0.16rem 0.4rem 0 rgba(255, 52, 52, 0.05);
          box-shadow: 0 0.16rem 0.4rem 0 rgba(255, 52, 52, 0.05);
  border-radius: 0.05333333rem;
}
.page-goods-list .goods-panel--join:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 1.33333333rem;
  height: 1.06666667rem;
  background: url(../../assets/img/join@3x.png) no-repeat center;
  background-size: auto 100%;
}
.page-goods-list .goods-panel + .goods-panel {
  margin-top: 0.21333333rem;
}
.page-goods-list .goods-head {
  position: relative;
  z-index: 1;
}
.page-goods-list .goods-head .head-img {
  display: inline-block;
  vertical-align: middle;
  width: 1.17333333rem;
  height: 1.17333333rem;
  background: #efefef;
  border-radius: 50%;
  overflow: hidden;
}
.page-goods-list .goods-head .head-img img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.page-goods-list .goods-head .head-info {
  display: inline-block;
  vertical-align: middle;
  padding: 0.10666667rem 0 0 0.21333333rem;
}
.page-goods-list .goods-head .head-info .info-name {
  display: inline-block;
  height: 0.53333333rem;
  line-height: 0.58666667rem;
  font-size: 0.37333333rem;
  font-weight: bold;
  color: #333;
  vertical-align: baseline;
}
.page-goods-list .goods-head .head-info .info-line {
  color: #d9d9d9;
  font-size: 0.21333333rem;
  vertical-align: 0.05333333rem;
  margin: 0 0.06666667rem;
}
.page-goods-list .goods-head .head-info .info-work {
  font-size: 0.29333333rem;
  color: #999;
  vertical-align: baseline;
}
.page-goods-list .goods-head .head-info .info-heart {
  position: relative;
  top: -0.04rem;
  height: 0.53333333rem;
  line-height: 0.53333333rem;
  font-size: 0.32rem;
  color: #ccc;
  /*进行中*/
  /*结束*/
}
.page-goods-list .goods-head .head-info .info-heart .jd-integer {
  position: relative;
  left: -0.13333333rem;
  top: -0.01333333rem;
}
.page-goods-list .goods-head .head-info .info-heart > i {
  display: inline-block;
  width: 0.53333333rem;
  height: 0.53333333rem;
  background: url(../../assets/img/heart@3x.jpg) no-repeat center;
  background-size: auto 100%;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
  opacity: .3;
}
.page-goods-list .goods-head .head-info .info-heart.info-heart--active {
  color: #ff3434;
}
.page-goods-list .goods-head .head-info .info-heart.info-heart--active > i {
  background: url(../../assets/img/heart.gif) no-repeat center;
  background-size: auto 100%;
  -webkit-filter: none;
          filter: none;
  opacity: 1;
}
.page-goods-list .goods-head .head-info .info-heart.info-heart--end {
  color: #ff3434;
}
.page-goods-list .goods-head .head-info .info-heart.info-heart--end > i {
  -webkit-filter: none;
          filter: none;
  opacity: 1;
}
.page-goods-list .goods-head .head-time {
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 0.32rem;
  color: #000;
}
.page-goods-list .goods-head .head-time > span {
  vertical-align: middle;
}
.page-goods-list .goods-head .head-time .jd-timer {
  white-space: nowrap;
  margin-left: 0.06666667rem;
  vertical-align: middle;
}
.page-goods-list .goods-head .head-over {
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 0.32rem;
  font-weight: 300;
  color: #fff;
  background: #999;
  border-radius: 0.33333333rem;
  padding: 0.06666667rem 0.2rem;
}
.page-goods-list .goods-title {
  margin-top: 0.32rem;
  font-size: 0.37333333rem;
  color: #333;
  max-height: 13333.32rem;
  font-weight: 300;
}
.page-goods-list .goods-img {
  font-size: 0;
  margin-top: 0.32rem;
}
.page-goods-list .goods-img > li {
  display: inline-block;
  width: 33.33%;
  height: 2.13333333rem;
}
.page-goods-list .goods-img > li div {
  height: 100%;
  background: #efefef;
  border-radius: 0.06666667rem;
}
.page-goods-list .goods-img > li div img {
  width: 100%;
  height: 100%;
  border-radius: 0.05333333rem;
}
.page-goods-list .goods-img > li:nth-child(-n+2) {
  padding-right: 0.08rem;
}

/*安卓下一些特殊处理*/
.underAndroid .page-goods-list .jd-timer-item {
  padding-top: 0.13333333rem;
}


