
@charset "UTF-8";
.jd-bar {
  background: #fff;
  height: 100%;
  border-radius: 1.33333333rem;
  font-size: 0.34666667rem;
  color: #F63459;
  font-family: 'jdzh-r';
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}
.jd-bar-cover {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(6%, #FFEBC9), to(#FFBD84));
  background: linear-gradient(-180deg, #FFEBC9 6%, #FFBD84 100%);
  border-radius: 1.33333333rem 0 0 1.33333333rem;
  float: left;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: all ease 1s;
  transition: all ease 1s;
  width: 20%;
  padding-top: 0.01333333rem;
}
.jd-bar-cover:after {
  content: '';
  position: absolute;
  top: 0;
  right: -0.01333333rem;
  width: 0.2rem;
  height: 0.38666667rem;
  background: url("../../../assets/img/barArrow.svg") no-repeat center;
  background-size: cover;
}
.jd-bar-rest {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  float: right;
  border-radius: 0 1.33333333rem 1.33333333rem 0;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-top: 0.01333333rem;
  -webkit-transition: all ease 1s;
  transition: all ease 1s;
}
.jd-bar.jd-bar--100 .jd-bar-cover {
  border-radius: 1.33333333rem;
}
.jd-bar.jd-bar--100 .jd-bar-cover:after {
  display: none;
}
.jd-bar.jd-bar--100 .jd-bar-rest {
  font-size: 0;
}

/*安卓下一些特殊处理*/
.underAndroid .jd-bar {
  -webkit-transform: none;
          transform: none;
}
.underAndroid .jd-bar .jd-bar-cover,
.underAndroid .jd-bar .jd-bar-rest {
  padding-top: 0.06666667rem !important;
}


