
@charset "UTF-8";
.jd-timer {
  display: inline-block;
  vertical-align: middle;
  font-family: 'jdzh-r';
}
.jd-timer > span {
  display: inline-block;
  vertical-align: middle;
  padding: 0;
}
.jd-timer-item {
  display: inline-block;
  background: #333;
  width: 0.37333333rem;
  line-height: normal;
  overflow: hidden;
  border-radius: 0.06666667rem;
  font-size: 0.26666667rem;
  color: #fff;
  padding: 0.08rem 0;
  text-align: center;
  vertical-align: middle;
}

/*安卓下一些特殊处理*/
.underAndroid .jd-timer > span {
  padding-top: 0.05333333rem;
}


